//  
type FunctionLike = Function;
//   
type EventKey = string;

/**
 *   .
 */
type VMEventListener = { [key: EventKey]: Function[] };
type EventNames = "ready" | "mounted" | "destroy";

/**
 * @class EventEmitter
 * @description
 *    .
 */
export class EventEmitter {
  constructor(private _listeners: VMEventListener = {}) {
    this._listeners = {};
  }

  /**
   *   .
   *
   * @param eventName   
   * @param func
   * @returns
   */
  on(eventName: EventNames, func: FunctionLike) {
    if (!this._listeners[eventName]) {
      this._listeners[eventName] = [];
    }

    if (!func) {
      return;
    }

    if (func instanceof Function) {
      this._listeners[eventName].push(func);
    }
  }

  /**
   *   .
   *
   * @param eventName   
   * @param args
   * @returns
   */
  emit(eventName: EventNames, ...args: any[]) {
    if (!this._listeners[eventName]) {
      return;
    }

    this._listeners[eventName].forEach((func) => {
      if (!(func instanceof Function)) {
        console.warn("    .");
        return;
      }

      func(...args);
    });
  }
}
